import StatisticsCard from '@/components/cards/StatisticsCard';
import { Card } from '@/types/statistics';

interface Props {
    cards: Card[];
    numJustifyStart?: boolean;
}

const StatisticsCards = ({ cards, numJustifyStart }: Props) => {
    return (
        <div className="grid grid-cols-[repeat(auto-fit,minmax(258px,1fr))] gap-6 justify-center sm:justify-between">
            {cards.map((e, index) => {
                return (
                    <StatisticsCard
                        key={index}
                        text={e.text}
                        num={e.num}
                        percent={e.percent}
                        icon={e.icon}
                        negative={e.negative}
                        label={e.label}
                        numJustifyStart={numJustifyStart}
                    />
                );
            })}
        </div>
    );
};

export default StatisticsCards;
