import { cn } from '@/lib/utils';
import React from 'react';

interface IconProps {
    num?: number | string;
    text?: string;
    icon?: React.ReactNode;
    percent?: number;
    className?: string;
    label?: string;
    negative?: boolean;
    numJustifyStart?: boolean;
}

const StatisticsCard = ({
    num,
    text,
    icon,
    percent,
    className = '',
    label,
    negative = false,
    numJustifyStart = false,
}: IconProps) => {
    return (
        <div
            className={cn(
                'relative bg-[#E8EFF666] ps-6 pe-4 py-4 shadow-[0px_2px_12px_0px_#14142B14] rounded-[8px] flex flex-row items-center justify-between border border-solid border-primery-blue-100',
                className,
            )}
        >
            <div className="flex items-start justify-center flex-col">
                {num !== undefined && (
                    <div
                        className={cn(
                            'w-full flex flex-row items-center justify-center gap-[6px] text-[32px] leading-[48px] text-primery-blue-900 font-bold tracking-normal',
                            { 'justify-start': label || numJustifyStart },
                        )}
                    >
                        {num}
                        {label && (
                            <div className="leading-[24px] text-base text-primery-blue-900 font-medium tracking-normal">
                                {label}
                            </div>
                        )}
                    </div>
                )}
                {text && (
                    <div className="text-nowrap min-w-[73px] font-semibold w-fit pb-[10.5px] text-4 text-primery-blue-500 leading-[20px] tracking-normal text-center border-b border-primery-blue-500">
                        {text}
                    </div>
                )}
            </div>
            <div className="flex flex-col h-full">
                {icon && (
                    <div className="rounded-full bg-primery-blue-100 w-14 h-14 flex flex-col items-center justify-center">
                        {icon}
                    </div>
                )}
                {percent !== undefined && (
                    <div
                        dir="ltr"
                        className={cn(
                            'absolute bottom-0 left-0 text-[#ffffff] text-xs leading-[13.8px] font-normal tracking-normal mt-auto ms-auto flex items-center justify-center w-[53px] h-[22px] py-1 px-4 rounded-tl-[4px] rounded-tr-[4px] rounded-bl-[8px]',
                            { 'bg-[#F59E0B]': percent === 0 },
                            { 'bg-[#F23030]': percent < 0 && !negative },
                            { 'bg-[#22AD5C]': percent > 0 && !negative },
                            { 'bg-[#F23030]': percent > 0 && negative },
                        )}
                    >
                        {percent}
                        <span>%</span>
                    </div>
                )}
            </div>
        </div>
    );
};

export default StatisticsCard;
